import { Component, OnInit, ViewEncapsulation, AfterViewInit, ElementRef, ViewChild, HostBinding} from '@angular/core';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FormControl, Validators, FormGroup, ValidationErrors} from '@angular/forms';
import {HttpClientModule} from '@angular/common/http';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { Helpers } from '../../../../../helpers';
import { ScriptLoaderService } from '../../../../../_services/script-loader.service';
import { FLY_IN_OUT_ANIMATION }   from '../../../../../animations/fly-in-out.animation';
import { ROUND_ANTICLOCK_ANIMATION }   from '../../../../../animations/round-anticlock.animation';
import { Router, Event, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { ToastComponent } from '../../../../layouts/toast-notification/toast.component';
import 'rxjs/add/operator/filter' ;

const EMAIL_REGEX = /^[a-zA-Z0-9.!#$%&â€™*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;
const MOBILE_REGEX=/^[91]{2}-\d{10}$/;
const PIN_REGEX=/^\d{6}$/;
const NUM_REGAX=/[0-9]/;

@Component({
    selector: ".m-grid__item.m-grid__item--fluid.m-wrapper",
    templateUrl: "./jc-priority-add.component.html",
    encapsulation: ViewEncapsulation.None,
	animations: [ 
    	FLY_IN_OUT_ANIMATION,
		ROUND_ANTICLOCK_ANIMATION
  ],
})

export class JcPriorityAddComponent  {
@HostBinding('@flyInOutTrigger') flyInOutTrigger = 'in';
@ViewChild(ToastComponent) toast: ToastComponent;

 
jcPriorityForm: FormGroup;
priorityjcId = 0;  
ajaxData : JcPriorityData[];
jcpriority= new JcPriorityData();
status;

msg = [];
bootstrapFormValues = [];
bootstrapMsg='';
jcprioritydatemsg:string;
 
constructor(private _script: ScriptLoaderService, 	private http: HttpClient, private _router: Router, private route:ActivatedRoute) {
	this.route.queryParams.filter(params => params.priorityjcId).subscribe(params=>{
			if(params.priorityjcId != undefined){
				this.priorityjcId=params.priorityjcId; 
			}
		}); 
	console.log("priorityjcId=="+this.priorityjcId);
	if(this.priorityjcId == 0){
		this.status = "Add";
	}else{
		this.status = "Update";
		this.populateData();
	}
	}
	
    ngOnInit() {
		this.jcPriorityForm = new FormGroup({			
			'priorityjcName' : new FormControl('', [ Validators.required]),
			'priorityjcDesc' : new FormControl('', [ Validators.required]),
			'priorityjcTrigger1Hrs' : new FormControl(''),
			'priorityjcTrigger2Hrs' : new FormControl(''),
			'priorityjcTrigger3Hrs' : new FormControl(''),
			'priorityjcTrigger4Hrs' : new FormControl(''),
			'priorityjcTrigger5Hrs' : new FormControl(''),
			'priorityjcEntryId' : new FormControl(''),
			'priorityjcEntryDate' : new FormControl(''),
			'priorityjcModifiedId' : new FormControl(''),
			'priorityjcModifiedDate' : new FormControl(''),
		});
		
	}
	  
	get priorityjcName(){ return this.jcPriorityForm.get('priorityjcName')};
	get priorityjcDesc(){ return this.jcPriorityForm.get('priorityjcDesc')};
	get priorityjcTrigger1Hrs(){ return this.jcPriorityForm.get('priorityjcTrigger1Hrs')};
	get priorityjcTrigger2Hrs(){ return this.jcPriorityForm.get('priorityjcTrigger2Hrs')};
	get priorityjcTrigger3Hrs(){ return this.jcPriorityForm.get('priorityjcTrigger3Hrs')};
	get priorityjcTrigger4Hrs(){ return this.jcPriorityForm.get('priorityjcTrigger4Hrs')};
	get priorityjcTrigger5Hrs(){ return this.jcPriorityForm.get('priorityjcTrigger5Hrs')};
 	get priorityjcEntryId(){ return this.jcPriorityForm.get('priorityjcEntryId')};
	get priorityjcEntryDate(){ return this.jcPriorityForm.get('priorityjcEntryDate')};
	get priorityjcModifiedId(){ return this.jcPriorityForm.get('priorityjcModifiedId')};
	get priorityjcModifiedDate(){ return this.jcPriorityForm.get('priorityjcModifiedDate')};
 
	  populateData(){
				const headers = new HttpHeaders({'Content-Type':'application/json'});
				this.http.post<JcPriorityData>(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jcpriority-data ',
										JSON.stringify({ 'priorityjcId' :this.priorityjcId}),
									{ headers, withCredentials: true }).subscribe(data =>{
																		this.jcpriority = data['populatadata'][0];
																		console.log("priorityjcModifiedId=="+this.jcpriority.priorityjcModifiedId);
																		},
															 (err) => {
																	/* this function is executed when there's an ERROR */
																	console.log("ERROR: "+err);
															 });
	 }
 
	   ngAfterViewInit(){ }	
	
 
		 toggleAnimation(){
			this._router.navigate(['service/jc-priority-list']);      
		 }

 
	  	onSubmit() {
			this.bootstrapFormValues = $('#m_form_1').serializeArray();

			for(var i=0;i<this.bootstrapFormValues.length;i++){
				this.jcpriority[this.bootstrapFormValues[i].name]=this.bootstrapFormValues[i].value;
				
				if(this.bootstrapFormValues[i].name=="priorityjcDuehrs"){
					if(this.bootstrapFormValues[i].value==""){
						this.bootstrapMsg = this.bootstrapMsg +  'Enter Due Date!<br>';
						this.jcprioritydatemsg="<font class='errorMsg'>Enter Due Date</font>";
						
					}else{
						this.jcprioritydatemsg = "";
					}
				}
			}
			if(this.bootstrapMsg != ''){
					this.msg[0]='Error';
					this.msg[1]='Enter the Mandatory Fields' ;
					this.toast.showtoast(this.msg);
					this.bootstrapMsg='';
			}
		
			if(!this.jcPriorityForm.invalid && this.bootstrapMsg == ''){
				  const headers = new HttpHeaders({'Content-Type':'application/json'});
			if(this.priorityjcId == 0){ 
				this.jcpriority['requestType'] = 'add';
			}else{
				this.jcpriority['requestType'] = 'update';
				this.jcpriority['priorityjcId'] = this.priorityjcId+'';
			}
			 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jcpriority',
									JSON.stringify(this.jcpriority),
						{ headers, withCredentials: true }).subscribe(data => {
															if(data['successmsg'] == undefined){
																this.msg[0]='Error';
																this.msg[1]=data['errormsg'];
															}else{
																if(data['successmsg'].includes('Successfully')){
																	this.msg[0]='success';
																	this.msg[1]=data['successmsg'];
																}
															}
	//			  											this.toast.showtoast(this.msg);
															if(this.msg[0]!='Error'){
																this.jcPriorityForm.reset();
																this.toggleAnimation();
															}			 
														},
														err =>{
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
		  } 
			console.log(this.jcpriority);
	  }
 
  onDelete(){
		 this.jcpriority['requestType'] = 'delete';
		this.jcpriority['priorityjcId'] = this.priorityjcId+'';
		 const headers = new HttpHeaders({'Content-Type':'application/json'});
		 this.http.post(sessionStorage.getItem('requestUrl')+'/axelacrm/portal/jcpriority',
									JSON.stringify(this.jcpriority),
						{ headers, withCredentials: true }).subscribe(data => {			 											
			 											if(data['successmsg'].includes('Successfully')){
															this.msg[0]='success';
															this.msg[1]=data['successmsg'];
														}else{
															this.msg[0]='Error';
															this.msg[1]=data['successmsg'];
														}
//			 											this.toast.showtoast(this.msg);
			 											this.toggleAnimation();
			 											this.jcPriorityForm.reset();
//			 											this.list.getData();
														},
														err =>{
			 												console.log(err['error']['desc']);
			 												this.msg[0]='Error';
			 												this.msg[1]=err['error'];
//			 												this.toast.showtoast(this.msg);
		 												});
	 }
	  	
	
}

	
export class  JcPriorityData {
    priorityjcId: string;
    priorityjcName: string;
	priorityjcDesc:string;
	priorityjcRank:string;
	priorityjcDuehrs:string='';
	priorityjcTrigger1Hrs:string;
	priorityjcTrigger2Hrs:string;
	priorityjcTrigger3Hrs:string;
	priorityjcTrigger4Hrs:string;
	priorityjcTrigger5Hrs:string;
	priorityjcEntryId: string;
	priorityjcEntryDate: string;
	priorityjcModifiedId: string;
	priorityjcModifiedDate: string;
  
}


